* {
  box-sizing: border-box;
}

.main-block {
  background-color: #F6F7F9;
  padding: 32px;

  &__items {
    margin-bottom: 26px;
  }

  &__flex {

    margin-bottom: 32px;

    @media (max-width: 992px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }

  &__text {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #90A3BF;
  }

  &__text--blue {
    color: #3563E9;
  }

  &__link {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #3563E9;
  }

  @media (max-width: 992px) {
    padding: 24px;
    display: flex;
    flex-direction: column;
  }
}

main {
  margin-top: 124px;

  @media (max-width: 768px) {
    margin-top: 222px;
  }
}

.navbar {
  background: #FFFFFF;
  border: 1px solid rgba(195, 212, 233, 0.4);
  padding: 0 32px;
  height: 124px;

  &__toggler-icon {
    background-image: url("./img/header/menu.svg");
  }

  &__item {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: #3D5278;
    margin-bottom: 24px;
  }

  &__link {
    text-decoration: none;
    color: #3D5278;
  }

  &__logo {
    font-family: 'Plus Jakarta Sans', sans-serif;
    font-style: normal;
    color: #3563E9;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    margin: 0 64px 0 28px;

    @media (max-width: 1200px) {

      font-size: 24px;
      margin: 0 32px 0 14px;
    }

    @media (max-width: 768px) {
      margin: 0;
    }
  }

  &__search {
    border: 1px solid rgba(195, 212, 233, 0.4);
    border-radius: 70px;
    width: 492px;
    padding: 12px;
    padding-left: 64px;

    &::placeholder {
      color: #3D5278;
      font-family: 'Plus Jakarta Sans';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      letter-spacing: -0.02em;
    }

    @media (max-width: 1200px) {
      width: 290px;
    }

    @media (max-width: 768px) {
      border: 1px solid rgba(195, 212, 233, 0.4);
      border-radius: 10px;
      padding: 11px 0 11px 56px;
    }
  }

  &__search-icon {
    height: 22px;
    z-index: 1;
    top: 12px;
    bottom: 12px;
    left: 22px;
    cursor: pointer;
    transition: all .2s ease-in-out;

    &:hover {
      transform: scale(1.1)
    }

    @media (max-width: 768px) {

      height: 19px;
      z-index: 1;
      top: 14px;
      bottom: 14px;
      left: 26px;
    }
  }

  &__filter-icon {
    height: 24px;
    z-index: 1;
    top: 12px;
    bottom: 12px;
    right: 20px;
    cursor: pointer;
    transition: all .2s ease-in-out;

    &:hover {
      transform: scale(1.1)
    }
  }

  &__items {
    gap: 20px;
  }

  &__item {
    border-radius: 50%;

    &:hover {
      transition-duration: 0.5s;
      background-color: rgba(195, 212, 233, 0.4);
    }
  }

  &__filter-icon-container {
    border: 1px solid rgba(195, 212, 233, 0.4);
    border-radius: 10px;
    height: 48px;
    width: 48px;
  }

  @media (max-width: 768px) {
    padding: 0 24px;
    height: 222px;
  }
}

.view-card {
  max-width: 492px;
  margin-right: 16px;

  & img {
    transition: all .2s ease-in-out;

    &:hover {
      transform: scale(1.03)
    }
  }

  &__main-image-container {
    border-radius: 10px;
    min-height: 360px;
    background-color: #3563E9;
    padding: 24px;
  }

  &__image-card {
    border-radius: 8px;
  }

  &__image-cards img {
    border-radius: 8px;
    max-width: 100%;
  }

  @media (max-width: 992px) {
    margin: 0 0 32px 0;
  }

  @media (max-width: 576px) {
    margin: 0 0 32px 0;
    max-width: 327px;
  }
}

.product-card {
  max-width: 492px;
  border-radius: 10px;
  margin-left: 16px;
  max-width: max-content;
  transition: all .2s ease-in-out;

  &:hover {
    transform: scale(1.03)
  }

  &__like--true {
    height: 18px;
    width: 20px;
    background: url(./img/main/like-active.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__like--false {
    height: 18px;
    width: 20px;
    background: url(./img/main/like-not-active.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__star {
    height: 17px;
    width: 16px;
    background: url(./img/main/star.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__star:not(:last-child) {
    margin-right: 5px;
  }

  &__star:last-child {
    margin-right: 10px;
  }

  &__stars--1 &__star:nth-child(-n+1),
  &__stars--2 &__star:nth-child(-n+2),
  &__stars--3 &__star:nth-child(-n+3),
  &__stars--4 &__star:nth-child(-n+4),
  &__stars--5 &__star:nth-child(-n+5) {
    background: url(./img/main/active-star.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__title {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 140%;
    color: #1A202C;
    margin-bottom: 0;
  }

  &__review {
    padding: 1px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: #3D5278;
  }

  &__feedback {
    margin-bottom: 34px;
  }

  &__text {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 200%;
    letter-spacing: -0.02em;
    color: #3D5278;
    margin-bottom: 32px;

    @media (max-width: 1435px) {
      font-size: 16px;
    }
  }

  &__flex-item {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: -0.02em;
    color: #90A3BF;
    margin-bottom: 68px;

    @media (max-width: 1435px) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__flex-item--black {
    font-weight: 600;
    color: #3D5278;
  }

  &__button {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    width: 140px;
    height: 56px;
    background: #3563E9;
    border-radius: 4px;
    border: 1px solid #3563E9;

    &:hover {
      transition-duration: 0.5s;
      background: rgba(0, 0, 0, 0);
      color: #3563E9;
      box-shadow: inset 0 0 0 3px #3563E9;
    }
  }

  &__price {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #90A3BF;
  }

  &__price span {
    font-size: 28px;
    line-height: 35px;
    color: #1A202C;

    @media (max-width: 1435px) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  @media (max-width: 992px) {
    margin: 0;
    max-width: 492px;
  }

  @media (max-width: 576px) {
    max-width: 327px;
  }
}

.review-block {
  padding: 24px;
  margin-bottom: 42px;
  max-width: max-content;

  &__title {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: -0.02em;
    color: #1A202C;
    margin: 0 12px 32px 0;
  }

  &__name {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: #1A202C;
  }

  &__date {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #90A3BF;
  }

  &__job-title {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    color: #90A3BF;
    margin: 0 0 12px 0;
  }

  &__text {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 200%;
    letter-spacing: -0.02em;
    color: #3D5278;
  }

  & a {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #90A3BF;
    margin-bottom: 32px;

    @media (max-width: 992px) {
      max-width: 492px;
    }

    @media (max-width: 576px) {
      margin-bottom: 26px;
      max-width: 327px;
    }
  }
}

.car-catalog {

  &__card {
    padding: 24px;
    border-radius: 10px;
    transition: all .2s ease-in-out;

    &:hover {
      transform: scale(1.03)
    }
  }

  &__card:not(:last-child) {
    margin-right: 32px;
  }

  &__title {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: #1A202C;
  }

  &__category {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    color: #90A3BF;
    margin-bottom: 52px;
  }

  &__image-container {
    margin-bottom: 52px;
    display: flex;
    justify-content: center;
  }

  &__image-container img {
    max-width: 100%;
  }

  &__feature-items {
    margin-bottom: 36px;
  }

  &__feature {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #90A3BF;
  }

  &__button {
    width: 116px;
    height: 44px;
    background: #3563E9;
    border-radius: 4px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    border: 1px solid #3563E9;

    &:hover {
      transition-duration: 0.5s;
      background: rgba(0, 0, 0, 0);
      color: #3563E9;
      box-shadow: inset 0 0 0 3px #3563E9;
    }
  }
}

.sidebar {
