    font-weight: 600;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: #94A7CB;
    margin-bottom: 28px;
  }

  &__type-block {
    margin-bottom: 56px;
  }

  &__capacity-block {
    margin-bottom: 56px;
  }

  &__range-title {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: -0.02em;
    color: #3D5278;
  }
}

.form-chek {
  margin: 0;
}

.form-check-label {
  font-family: 'Plus Jakarta Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: -0.02em;
  color: #3D5278;
  margin-bottom: 32px;
}

.form-check-label span {
  color: #90A3BF;
}

.form-range {
  margin: 4px 0 16px 0;
}

.footer {
  padding: 80px 60px 60px 60px;

  &__logo {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    color: #3563E9;
  }

  &__text {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 200%;
    letter-spacing: -0.01em;
    color: rgba(19, 19, 19, 0.6);

