  }

  &__item-text {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: rgba(19, 19, 19, 0.6);
  }

  &__privacy {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 200%;
    letter-spacing: -0.01em;
    color: #131313;
  }

  &__privacy-item {
    margin-right: 60px;
  }

  @media (max-width: 576px) {
    padding: 24px;
    background-color: #F6F7F9;
  }
}

.scroll {
  @media (max-width: 992px) {
    width: 492px;
    overflow-x: scroll;
  }

  @media (max-width: 576px) {
    width: 327px;
    overflow-x: scroll;
  }
}


.hover-image-scale {
  display: inline-block;
  overflow: hidden;
  /* Скрываем всё за контуром */
}

.hover-image-scale img {
  transition: 1s;
  /* Время эффекта */
  display: block;
}

.hover-image-scale img:hover {
  transform: scale(1.2);
  /* Увеличиваем масштаб */
}


.review-block__button a,
.main-block__link {
  position: relative;
  cursor: pointer;
}

.review-block__button a:after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: -4px;
  width: 0;
  height: 2px;
  background-color: #3563E9;
  transition: width 0.5s;
}

.review-block__button a:hover:after {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  bottom: -4px;
  height: 2px;
  /* Высота линии */
  background-color: #90A3BF;
  transition: width 0.5s;
}

.main-block__link:after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: -4px;
  width: 0;
  height: 2px;
  background-color: #90A3BF;
  transition: width 0.5s;
}

.main-block__link:hover:after {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  bottom: -4px;
  height: 2px;
  /* Высота линии */
  background-color: #3563E9;
  transition: width 0.5s;
}